.score-board {
  position: fixed;
  top: 0;
  width: 125px;
  height: 125px;
  cursor: pointer;

  display: flex;
  flex-direction: row;
  align-items: center;
}

.score-board p {
  font-size: 96px;
  padding-bottom: 7.5px;

  margin: 0 auto;
}

.left-score {
  justify-content: flex-start;
  background-color: #36DBD7;
  border-bottom-right-radius: 50%;
  left: 0;
}

.right-score {
  justify-content: flex-end;
  background-color: #F62459;
  border-bottom-left-radius: 50%;
  right: 0;
}
.boxes {
  display: grid;
  grid-template-rows: repeat(3, 1fr);
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 3.5px;

  position: absolute;
  width: 501px;
  height: 501px;
}

.box {
  width: 160px;
  height: 160px;
  cursor: pointer;
}

svg {
  stroke: rgb(0, 0, 0);
  width: 501px;
  height: 501px;
  stroke-width: 10;
  stroke-linecap: round;
  stroke-linejoin: round;
  fill: none;
}

line {
  stroke: white;
  stroke-dasharray: 1000;
  stroke-dashoffset: 1000;
  -webkit-animation: dash 1.25s cubic-bezier(0.65, 0, 0.45, 1) forwards;
          animation: dash 1.25s cubic-bezier(0.65, 0, 0.45, 1) forwards;
}

circle {
  stroke: #F62459;
  stroke-width: 12.5;
  stroke-dasharray: 1000;
  stroke-dashoffset: 1000;
  -webkit-animation: dash 1s cubic-bezier(0.65, 0, 0.45, 1) forwards;
          animation: dash 1s cubic-bezier(0.65, 0, 0.45, 1) forwards;
}

path {
  stroke: #36DBD7;
  stroke-width: 12.5;
  stroke-dasharray: 1000;
  stroke-dashoffset: 1000;
  -webkit-animation: dash 1s cubic-bezier(0.65, 0, 0.45, 1) forwards;
          animation: dash 1s cubic-bezier(0.65, 0, 0.45, 1) forwards;
}

@-webkit-keyframes dash {
  to {
    stroke-dashoffset: 0;
  }
}

@keyframes dash {
  to {
    stroke-dashoffset: 0;
  }
}

@media (max-width: 525px) {
  svg {
    width: 300px;
    height: 300px;
  }
}
.settings-button, .about-button {
  width: 125px;
  height: 125px;
  position: fixed;
  bottom: 0;
  cursor: pointer;
}

.settings-button {
  background-color: #F62459;
  border-top-right-radius: 125px;
  left: 0;
}

.about-button {
  background-color: #36DBD7;
  border-top-left-radius: 125px;
  right: 0;
}

.settings-button .button-icon {
  position: fixed;
  bottom: 25px;
  left: 25px;
  font-size: 48px;
}

.about-button .button-icon {
  position: fixed;
  bottom: 25px;
  right: 25px;
  font-size: 48px;
}

/* SWEET ALERT */

.see-github {
  text-decoration: none;
  color: #2196f3;
  font-weight: 600;
  display: flex;
}

/* OPTIONS */
.options-items {
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin-bottom: 25px;
}

.options-items:first-child {
  margin-top: 25px;
}

.options-item {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

.options-item ion-icon {
  margin-bottom: 10px;
  font-size: 48px;
}

.active {
  background-color: #2B77C0;
  border-radius: 15px;
  width: 100px;
  height: 100px;
  padding: 10px;
  color: white;
}
.game {
  height: 100vh;
  width: 100vw;
  color: white;
  text-align: center;
  position: relative;

  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.light {
  background-color: #1F3A93;
}

.dark {
  background-color: gray;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Raleway', sans-serif;
}

.home {
  height: 100vh;
  width: 100vw;
  background-color: #044D6E;
}
